import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import {
  getCartItems,
  removeCartItem,
  onSuccessBuy,
} from "../../../_actions/user_actions";
import UserCardBlock from "./Sections/UserCardBlock";
import { Result, Empty, Row, Col, Button } from "antd";
import Axios from "axios";
import Paypal from "../../utils/Paypal";
import { Link } from "react-router-dom";

function CartPage(props) {
  const dispatch = useDispatch();
  const [Total, setTotal] = useState(0);
  const [ShowTotal, setShowTotal] = useState(false);
  const [ShowSuccess, setShowSuccess] = useState(false);

  useEffect(() => {
    let cartItems = [];
    if (props.user.userData && props.user.userData.cart) {
      if (props.user.userData.cart.length > 0) {
        props.user.userData.cart.forEach((item) => {
          cartItems.push(item.id);
        });
        dispatch(getCartItems(cartItems, props.user.userData.cart)).then(
          (response) => {
            if (response.payload.length > 0) {
              calculateTotal(response.payload);
            }
          }
        );
      }
    }
  }, [props.user.userData]);

  const calculateTotal = (cartDetail) => {
    let total = 0;

    cartDetail.map((item) => {
      total += parseInt(item.price, 10) * item.quantity;
    });

    setTotal(total);
    setShowTotal(true);
  };

  const removeFromCart = (productId) => {
    dispatch(removeCartItem(productId)).then((response) => {
      if (response.payload.cartDetail.length <= 0) {
        setShowTotal(false);
      } else {
        calculateTotal(response.payload.cartDetail);
      }
    });
  };

  const transactionSuccess = (data) => {
    dispatch(
      onSuccessBuy({
        cartDetail: props.user.cartDetail,
        paymentData: data,
      })
    ).then((response) => {
      if (response.payload.success) {
        setShowSuccess(true);
        setShowTotal(false);
      }
    });
  };

  const transactionError = () => {
    console.log("Paypal error");
  };

  const transactionCanceled = () => {
    console.log("Transaction canceled");
  };
  function formatNumber(num) {
    return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
  }

  return (
    <div
      style={{
        minHeight: "100vh",
        display: "flex",
        alignItems: "center",
      }}
    >
      <div
        style={{
          margin: "5rem auto",
          maxWidth: "1200px",
          padding: "0 1rem",
        }}
      >
        {ShowTotal ? (
          <div>
            <h1>Here's What You're Getting!</h1>{" "}
            <Row gutter={16}>
              {" "}
              <Col xs={24} sm={16}>
                {" "}
                <UserCardBlock
                  products={props.user.cartDetail}
                  removeItem={removeFromCart}
                />
              </Col>
              <Col xs={24} sm={8}>
                <div
                  style={{
                    padding: "1rem",
                    border: "1px solid #ddd",
                    boxShadow: "0 0 10px rgba(0,0,0,0.4)",
                    position: "-webkit-sticky",
                    position: "sticky",
                    top: "0",
                  }}
                >
                  {" "}
                  <Row justify="space-between">
                    <h1>Total amount:</h1>
                    <h1> ₱{formatNumber(`${Total}`)}</h1>{" "}
                  </Row>
                  <Paypal
                    toPay={Total}
                    onSuccess={transactionSuccess}
                    transactionError={transactionError}
                    transactionCanceled={transactionCanceled}
                  />
                </div>
              </Col>{" "}
              <Link to="/search" style={{ marginTop: "2rem" }}>
                {" "}
                <h4>&nbsp;{"<<"}Continue Booking</h4>
              </Link>
            </Row>
          </div>
        ) : ShowSuccess ? (
          <div style={{ textAlign: "center" }}>
            <Result status="success" title="Successfully Purchased Items" />
            <br />{" "}
            <Link to="/search">
              <Button type="primary">Continue Booking</Button>
            </Link>
          </div>
        ) : (
          <div
            style={{
              width: "100%",
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              textAlign: "center",
            }}
          >
            <br />
            <Empty description={false} />
            <p>No Items In the Cart</p>
            <br />
            <Link to="/search">
              <Button type="primary">Book Now</Button>
            </Link>
          </div>
        )}
      </div>

      {/* {ShowTotal && (
        <Paypal
          toPay={Total}
          onSuccess={transactionSuccess}
          transactionError={transactionError}
          transactionCanceled={transactionCanceled}
        />
      )} */}
    </div>
  );
}

export default CartPage;
